import { Component } from '@angular/core';
import { Router } from '@angular/router'

import { UserService } from '../../service/user.service/user.service';

@Component({
  selector: 'home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent {

  private slackTag:string = '';
  private slackKey:string = '';
  private err: string = '';

  private slackStatus:string;

  constructor(private userService:UserService, private router:Router) {

    if(!this.userService.isLoggedIn()) {
      this.router.navigate(['/login']);
    } else {
      localStorage.removeItem('slackStatus');
  
      userService.fetchProfile()
        .then(res => {
          if(res.status == 200) {
            let flag:boolean = false;
            flag = res.json().SocialNetworkAccounts.length > 0 && res.json().SocialNetworkAccounts.every(sna => {
              console.log(sna);
              return sna.SocialNetworkId == 1;
            });
            
            this.slackStatus = (flag)? 'complete' : 'wait';
            localStorage.setItem('slackStatus', this.slackStatus);
          }
        })
    }
  }

  LogOut() {
    this.err = '';
    this.userService.logout();
    this.router.navigate(['/login']);
    return false; 
  }

  slackRegister() {
    this.err = '';
    this.userService.slackRegister(this.slackTag)
      .then(() => {
        this.slackStatus = 'pending';
        localStorage.setItem('slackStatus', this.slackStatus);
      });
      return false; 
  }

  slackApprove() {
    this.err = '';
    this.userService.slackApprove(this.slackKey)
      .then((res) => {
        if(res.status == 200) {
          this.slackStatus = 'complete';
          localStorage.setItem('slackStatus', this.slackStatus);
        } else {
          this.err = res.statusText;
        }
      });
      return false; 
  }

  Reset() {
    this.err = '';
    this.slackStatus = 'wait';
    localStorage.setItem('slackStatus', this.slackStatus);
    return false;   
  }
}



