import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions, URLSearchParams } from '@angular/http';

import 'rxjs/add/operator/toPromise';

@Injectable()
export class UserService {
  private loggedIn = false;

  constructor(private http: Http) {
    this.loggedIn = !!localStorage.getItem('AccessToken');
  }

  fetchProfile() {
    let headers = new Headers();
    headers.append('Token', localStorage.getItem('AccessToken'));

    let options = new RequestOptions({headers: headers});
    return this.http.get('http://rating.incode-it.com/api/personal/current', options)
      .toPromise()
      .then((res:Response) => {
        return res;
      })
      .catch((err:any) => {
        console.log(err);
        return err;
      });
  }

  registration(email, login, password) {

    const body = {
      Email: email,
      Login: login,
      Password: password
    };

    return this.http
      .post('http://rating.incode-it.com/api/registration', body)
      .toPromise()
      .catch((err:any) => {
        console.log(err);
        return err;
      });
  }

  login(login, password) {
    const body = {
      Login: login,
      Password: password
    };

    return this.http
      .post('http://rating.incode-it.com/api/login', body)
      .toPromise()
      .then((res:Response) => {
        localStorage.setItem('AccessToken', res.json().AccessToken);
        this.loggedIn = true;

        return res;
      })
      .catch((err:any) => {
        console.log(err);
        return err;
      });
  }

  logout() {
    localStorage.removeItem('AccessToken');
    this.loggedIn = false;
  }

  isLoggedIn() {
    return this.loggedIn;
  }

  slackRegister(slackLogin) {
    let headers = new Headers();
    headers.append('Token', localStorage.getItem('AccessToken'));
    let params: URLSearchParams = new URLSearchParams();
    params.set('socialNetworkId', '1');
    params.set('login', slackLogin);

    let options = new RequestOptions({
      headers: headers,
      search: params
    });

    return this.http.post('http://rating.incode-it.com/api/SocialNetwork', {}, options)
      .toPromise()
      .then((res:Response) => {

          return res;
      })
      .catch((err:any) => {
        console.log(err);
        return err;
      });
  }

  slackApprove(key) {
    let headers = new Headers();
    headers.append('Token', localStorage.getItem('AccessToken'));
    let params: URLSearchParams = new URLSearchParams();
    params.set('key', key);

    let options = new RequestOptions({
      headers: headers,
      search: params
    });

    return this.http.put('http://rating.incode-it.com/api/SocialNetwork', {}, options)
      .toPromise()
      .then((res:Response) => {

          return res;
      })
      .catch((err:any) => {
        console.log(err);
        return err;
      });

  }
}



